
html {
  color: #979797;
}
body {
  background-color: #ececec;
  margin: 0;
}
ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
a {
  text-decoration: none;
}

#header {
  background-color: #327989;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 2rem 0;
  position: relative;
}
#header .photo {
    width: 5rem;
    height: 5rem;
    background-color: #000;
    position: absolute;
    left: 1rem;
    border-radius: 50%;
}
#header .main-nav {
    padding: 2rem 0;
}
#header .main-nav ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      padding: 0 5rem;
}
#header .sub-nav ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 0 10rem;
}
#header .sub-nav ul li {
      width: 2.5rem;
      height: 2.5rem;
      background-color: #000;
      border-radius: 50%;
}
.article {
  padding: 2rem 1rem;
  border-bottom: 1px solid #ddd;
}
.article .info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
.article .info .date {
      margin-right: 2rem;
}
.article .title {
    text-align: center;
    font-size: 2.5rem;
}
.article .detail {
    line-height: 1.5rem;
    font-size: 1.4rem;
}
.article .more {
    display: block;
    color: #076dd0;
    font-size: 1.5rem;
    margin-top: 1.5rem;
    text-align: center;
}
.nextPage {
  color: #076dd0;
  font-size: 1.5rem;
  text-align: center;
  padding: 1.5rem 0;
  border-bottom: 1px solid #ddd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
#footer {
  text-align: center;
  line-height: 2rem;
  padding: 1rem;
}

#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .white {
    background-color: #fff;
    margin-bottom: .5rem;
}
#app .auto {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: .9rem;
}
#app .sidebar {
    -webkit-box-flex: 3;
        -ms-flex: 3;
            flex: 3;
    margin-right: .5rem;
}
#app .sidebar .photo {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      padding: 2rem 0;
}
#app .sidebar .photo img {
        width: 5rem;
        height: 7rem;
        border: 0.2rem solid #e5e5e5;
}
#app .sidebar .name-block {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      text-align: center;
}
#app .sidebar .name-block .name {
        color: black;
        font-size: 2rem;
        margin-bottom: 0;
}
#app .sidebar .name-block .tip {
        margin-top: 0;
}
#app .sidebar .info-block {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
#app .sidebar .skill-block {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
#app .sidebar .skill-block .progess {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
#app .sidebar .skill-block .progess .exprice {
          -webkit-box-flex: 8;
              -ms-flex: 8;
                  flex: 8;
          height: 1rem;
          background-color: #000;
          position: relative;
}
#app .sidebar .skill-block .progess .exprice::after {
            content: '';
            width: 100%;
            height: 1rem;
            background-color: #f00;
            position: absolute;
            left: 0;
            top: 0;
}
#app .sidebar .skill-block .progess span {
          -webkit-box-flex: 2;
              -ms-flex: 2;
                  flex: 2;
}
#app .content {
    -webkit-box-flex: 4;
        -ms-flex: 4;
            flex: 4;
}
#app .content .item {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
#app .content .item-info {
      color: #838383;
}
#app .content .title {
      color: #334d68;
}
#app .content .item-content ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-flow: wrap;
          flex-flow: wrap;
}
#app .content .item-content ul li {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        width: 5rem;
}

